import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_SELECT_COLS = Symbol('TABLE_SELECT_COLS');

export function selectTableCols(
  table: Table,
  startIndex: number,
  endIndex: number,
) {
  return {
    type: TABLE_SELECT_COLS,
    payload: {
      node: table,
      startIndex,
      endIndex,
    },
  };
}

interface TableSelectTableColsPayload {
  node: Table;
  startIndex: number;
  endIndex: number;
}

interface TableSelectTableColsAction {
  type: typeof TABLE_SELECT_COLS;
  payload: TableSelectTableColsPayload;
}

export function isTableSelectTableColsAction(
  action: Action,
): action is TableSelectTableColsAction {
  return action.type === TABLE_SELECT_COLS;
}
